	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"A City Learns","date":"10 Mar 2003","slug":"/2003-03-10-a-city-learns/","tags":"Y2003"},"html":"<p>The Abendblatt on Saturday included an <a title=\"Hamburg - eine Stadt lernt\" href=\"http://www.abendblatt.de/daten/2003/03/08/131835.html\">excerpt</a> about Hamburg in 1946, one year after the war, from a special issue of the magazine <a href=\"http://www.geo.de/\">Geo</a> on \"Germany after the War: 1945-1955\". Somehow this <a href=\"http://www.geo.de/GEO/kultur_gesellschaft/geschichte/2003_02_epoche_alltag_in_truemmern/index.html?SDSID=88193100000021047332355\">report</a> on the city in ruins seems very appropriate right now, and not just because of my interest in Hamburg and the <em>Stunde Null</em>.</p>"},"next":{"fields":{"title":"I Fought the Ants but the Ants Won","date":"06 Mar 2003","slug":"/2003-03-06-i-fought-the-ants-but-the-ants-won/"}},"previous":{"fields":{"title":"Gonzo Gardening","date":"10 Mar 2003","slug":"/2003-03-10-gonzo-gardening/"}}},{"node":{"fields":{"title":"I Fought the Ants but the Ants Won","date":"06 Mar 2003","slug":"/2003-03-06-i-fought-the-ants-but-the-ants-won/","tags":"Y2003"},"html":"<p>Message to Mama, currently at a conference in Leipzig: ANT POISON NOT WORKING. STOP. SEND REINFORCEMENTS. STOP.</p>"},"next":{"fields":{"title":"The End of Consensus","date":"05 Mar 2003","slug":"/2003-03-05-the-end-of-consensus/"}},"previous":{"fields":{"title":"A City Learns","date":"10 Mar 2003","slug":"/2003-03-10-a-city-learns/"}}},{"node":{"fields":{"title":"The End of Consensus","date":"05 Mar 2003","slug":"/2003-03-05-the-end-of-consensus/","tags":"Y2003"},"html":"<p>When Gerhard Schröder was first elected Chancellor 5 years ago, the lynchpin of his economic strategy was the <em>Bündnis für Arbeit</em>, or Alliance for Jobs, meetings of top officials from industry and labor moderated by the Chancellor that would reach consensus on the measures needed for economic reform in Germany. </p>\n<p>In those five years, the Alliance for Jobs has sputtered off and on, mostly off, as industry and labor accused one another of sabotaging the talks. This winter Schröder tried to revive the Alliance, but after a preliminary meeting Monday he <a href=\"http://www.dw-world.de/english/0,3367,1430_A_792300_1_A,00.html\">declared the Alliance for Jobs for dead</a>, and that he would present his own proposals for economic reform in an address on March 14.</p>\n<p>This may be a significant move by Schröder to move away from the unions and show real leadership, a sign that he is willing to work together with opposition to pull Germany out of its economic crisis. Or it may be just a lot of hot air. But in case the change is for real, it has now begun.</p>"},"next":{"fields":{"title":"One, Two, Three","date":"05 Mar 2003","slug":"/2003-03-05-one-two-three/"}},"previous":{"fields":{"title":"I Fought the Ants but the Ants Won","date":"06 Mar 2003","slug":"/2003-03-06-i-fought-the-ants-but-the-ants-won/"}}}],"pathPrefix":"","first":false,"last":false,"index":781,"pageCount":1101}}

